@import url(https://fonts.googleapis.com/css2?family=Gruppo&display=swap);
body{margin:0}

.App{font-family:"Gruppo", cursive}

.blog-header{position:relative;height:20vh;width:100%;background-color:#fefefe;border-bottom:solid 1px #e2e2e2;font-family:"Gruppo", cursive;margin:0 auto}.title-group{display:flex;flex-direction:column;justify-content:center;align-items:center;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;margin-left:1vw}.blog-title{font-size:5rem;margin-bottom:0}.blog-subtitle{margin-top:0;align-self:flex-end}

.main{position:relative;display:flex;flex-direction:row;justify-content:space-between;margin:0}.main-content{display:flex;flex-direction:column;width:50vw;padding:0 10vw;margin:10vh 0;border-right:solid 1px #e2e2e2}.signature{height:4rem}

.article-links-top{display:flex;flex-direction:column;justify-content:space-around;align-items:center}.nav-icon{width:30px;padding:0.5rem;border:1px #c5c5c5 solid;cursor:pointer;transition:0.3s}.nav-icon:hover{transform:scale(0.9);transition:0.3s;background-color:rgba(230,230,243,0.623)}.article-date{font-size:1.2rem;margin:0 0 1rem}.nav-icon-twitter{outline:none}

.more-articles{width:30vw;background-color:#fefefe;display:flex;flex-direction:column;align-items:center}.article-nav-link{text-decoration:none;width:80%;transition:0.3s}.article-nav-link:hover{transform:scale(1.05);transition:0.3s}.more-articles-figure{display:flex;flex-direction:column;justify-content:center;align-items:center}.more-articles-image{width:100%}.more-articles-title{font-size:2rem}

.blog-footer{border-top:solid 1px #e2e2e2;background-color:#fcfcfc;height:20vh;color:white}

.article-header{display:flex;flex-direction:column;justify-content:space-between}.title-content{display:flex;flex-direction:column;width:100%}.article-version{font-size:1.2rem;margin:0}.title-row{width:100%;display:flex;flex-direction:row;justify-content:space-between}.article-title{font-size:4rem;margin-top:0}.article-content{line-height:1.5rem;font-size:20px}.article-main-figure{display:flex;flex-direction:column;justify-content:center;align-items:center;margin:6rem auto}.article-main-image{width:100%}.article-image-figure{width:70%;display:flex;flex-direction:column;justify-content:center;align-items:center;margin:4rem 15%}.article-image{width:100%}.header-bottom-row{display:flex;flex-direction:row;justify-content:space-between;align-items:center}

.author{display:flex;flex-direction:column;align-items:center;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.author-image{width:100px;height:100px;border-radius:100%}.author-name{font-size:1.1rem}

